import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { postOrderInfo } from '../../utils/api.js';

export const initialState = {
  data: [],
  isLoading: false,
  error: null
}
// 
export const fetchOrder = createAsyncThunk(  //    pending, fulfield, reject
    'order/fetchOrder', // 
    //        (,     ).  - peyload creater -  
    async (dataID, {dispatch, getState, rejectWithValue, fulfillWithValue}) => {       //  -   -     (    )     - 
        try {
            const data = await postOrderInfo(dataID);         
            return fulfillWithValue(data); //  (,    )    
        }
        catch (error) {
            if(error.status) {
                return rejectWithValue(error);
            }
            return rejectWithValue({errorMessage: '   '});
        }    
    }
)
// return      

//,    
export const orderSlice = createSlice({
  name: 'order',
  initialState,
  extraReducers: (builder) => {    // 
    builder 
        .addCase(fetchOrder.pending, (state) => {
            state.isLoading = true;
            state.error = null;
        })
        .addCase(fetchOrder.fulfilled, (state, action) => {
            state.isLoading = false;
            state.data = action.payload;
        })
        .addCase(fetchOrder.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
        })
  }
})

export default orderSlice.reducer;